import { Injectable } from '@angular/core'

import 'rxjs/Rx'
import { Observable, Subscribable } from 'rxjs/Observable'
import { Subscriber } from 'rxjs/Subscriber'
import { Subject } from 'rxjs/Subject'


export interface MIDIMessage {
      control: string
      name: string
      key: number
      keyName: string
      // in case of dials
      decimal?: number
      // in case of keys
      velocity?: number
}

declare var MIDI: any
declare var sketch: any

@Injectable()
export class MIDIService {

  // test: count downs
  downCount = 0

  /**
   * Observable and Stream for ALL midi messages
   */

  source: Subscriber<MIDIMessage>
  stream: Observable<MIDIMessage>

  /**
   * keyboard state: knobs
   */
  knobs = {}
  toggles = {}

  keyNameMap = {
    7: 'Master',
    91: 'R1',
    93: 'R2',
    74: 'R3',
    71: 'R4',
    73: 'R5',
    75: 'R6',
    72: 'R7',
    10: 'R8',
    // toggles
    97: 'B1',
    96: 'B2',
    66: 'B3',
    67: 'B4',
    64: 'B5',
    65: 'B6',
    // same as master, 7:  'B7',
    // same as R8, 10: 'B8',
    1: 'Modululation',
    // continue
  }
  player: any // midi.js player

  /**
   * loads then play a given song
   * @param song
   */
  public playSong(song) {
    console.log('play', song.name)
    this.player.loadFile(song.data, () => {
      console.log('song loaded')
      this.player.start()
    })
  }

  /**
   * Play a sound on the Web-Midi thing
   *
   * @param channel The MIDI channel
   * @param key the MIDI key
   * @param velocityDecimal the velocity between 0 and 1
   */
  soundNote(channel: number, key: number, velocityDecimal) {
    MIDI.noteOn(0, key, velocityDecimal * 127, 0)
  }

  /**
   * Send a 'note-off' signal to our midi js setup
   * @param channel
   * @param key
   * @param velocityDecimal 
   */
  stopNote(channel: number, key: number, velocityDecimal) {
     MIDI.noteOff(0, key, velocityDecimal)
  }

  constructor(
  ) {

    /**
     * create MIDI Stream
     */
    this.stream = new Observable(source => {
      this.source = source
    }).share()
    this.stream.subscribe()

    /**
     * set up knobs and defaults
     */
    for (let i = 1; i < 9; i ++) {
      // knobs are default 0.5
      let knobName = 'R' + i
      this.knobs[knobName] = 0.5

      // toggles are default OFF
      let toggleName = 'B' + i
      this.toggles[toggleName] = false
    }


    /**
     * get MIDI Keyboard
     */
    const nav: any = navigator
    if (nav.requestMIDIAccess) {
      nav.requestMIDIAccess({
        sysex: false
      }).then(this.onMIDISuccess, this.onMIDIFailure);
    } else {
      alert('no midi support, sorry')
    }

    /**
     * create MIDI Player
     */
    MIDI.loader = new sketch.ui.Timer
    MIDI.loadPlugin({
      soundfontUrl: '/assets/soundfont/',
      onprogress: function (state, progress) {
        MIDI.loader.setValue(progress * 100);
      },
      onsuccess: () => {
        /// player setup
        this.player = MIDI.Player
        // player speed
        this.player.timeWarp = 1

        // load song and do nothing
        this.player.loadFile(this.songList[0].data, () => {
          console.log('song loaded')
        })

        // listen to player events
        this.player.addListener((data) => {

          // let now = data.now; // where we are now
          // let end = data.end; // time when song ends
          // let channel = data.channel; // channel note is playing on

          let message = data.message; // 128 is noteOff, 144 is noteOn
          let note = data.note; // the note
          let velocity = data.velocity; // the velocity of the note

          if (message === 144) {
            this.downCount ++
          }

          // stream the event
          this.streamMessage({ data: [message, note, velocity] })
        })
      }
    })
  }

  /* browser and midi device success */

  onMIDISuccess = (midiAccess) => {
    console.log('browser and device good')

    // raw MIDI data, inputs, outputs, and sysex status
    const midi = midiAccess
    const inputs = midi.inputs.values()

    // for each of the incoming messages from a connected midi device
    for (let input = inputs.next(); input && !input.done; input = inputs.next()) {
      // call the onMIDIMessage function
      input.value.onmidimessage = this.streamMessage
    }
  }

  /** browser or device issues */
  onMIDIFailure = (error) => {
    console.log('No access to MIDI devices or your browser does not support WebMIDI API.')
  }

  /** take a message with raw data in data object
   *
   * outputs a nicely formatted midi message
   * updates internal representation
   */

  streamMessage = (message) => {

    const [action, key, value] = message.data

    // console.log('MIDI service received action, key, value: ', message.data)

    let msg
    let control
    let eventName
    let keyName
    let velocity

    switch (action) {
      case 144:
        msg = {
          control: 'keyboard',
          name: 'keydown',
          key: key,
          keyName: 'C1', // todo
          velocity: value / 127,
        }
        break

      case 128:
        msg = {
          control: 'keyboard',
          name: 'keyup',
          key: key,
          keyName: 'C1', // todo
          velocity: value / 127,
        }
        break

      case 176:
        msg = {
          control: 'dial',
          name: 'turn',
          key: key,
          keyName: this.keyNameMap[key],
          decimal: value / 127
        }
        // update local representation (does not work)
        const newVal = value / 127
        const knobName = this.keyNameMap[key]
        this.knobs[knobName] = newVal
        break
    }

    // pass down the message stream
    this.source.next(msg)
  }

  songList = [
    {
      name: 'Rustles of Spring Op-32 No-3',
      data: 'data:audio/midi;base64,',
    },
    {
      name: 'The EnterTainer',
      data: 'data:audio/midi;base64,',
    },
    {
      name: 'Tchaikovsky - Waltz of the Flowers',
      data: 'data:audio/midi;base64,',
    },
    {
      name: 'Granados - Oriental',
      data: 'data:audio/midi;base64,',
    },
    {
      name: 'Tchaikovsky - Arabian Dance',
      data: 'data:audio/midi;base64,'
    }
  ]
}




